<template>
	<div id="music-list">
		<searchbar></searchbar>
		<ul>
			<li class="music-item"
				v-for="(item, index) in musicList.songlist"
				@click="musicSelect(item)"
				>
				<div class="music-info">
					<div class="music-name">
						{{item.name}}
					</div>
					<div class="music-singer">
						<span v-for="(sub, index) in item.singer">
							{{sub.name}}
						</span>
						<span>-</span>
						<span v-for="(sub, index) in item">
							{{sub.title}}
						</span>
						<span>{{item.subtitle}}</span>
					</div>
				</div>
			</li>
		</ul>
	</div>
</template>
<script>
import searchbar from './searchbar.vue'
import { mapGetters } from 'vuex'
export default {
	name: "musiclist",
	data () {
		return {
			musicList:[]
		}
	},
	components: {
		searchbar
	},
	beforeCreated () {
		this.$store.dispatch('getCDList', this.$route.params.id).then((res) => {
			this.musicList = res.data.cdlist[0]
		})		
		document.body.scrollTop = 0
	},
	destroyed () {
	},
	activated () {
		this.$store.dispatch('getCDList', this.$route.params.id).then((res) => {
			this.musicList = res.data.cdlist[0]
		})
		document.body.scrollTop = 0
	},
	//activated 用于重新进行事件响应，不被keep-alive缓存
	methods: {
		previous (){
			this.$router.go(-1)
		},
		musicSelect(item){
			//直接修改state是否合适？
			this.$store.state.nowPlaying = item
			this.$store.state.playList = this.musicList.songlist
			this.$store.commit('play')
			this.$store.dispatch('albummid' ,item.mid)
		}
	}
}
</script>
<style lang="css" scoped>
#music-list {
	background: #fff;
}
#music-list .music-item{
	margin: 5px 0 0 5px;
}
#music-list li:nth-child(2n){
	background: gainsboro
}
.music-info {
	font-size: 14px;
}
.music-singer {
	font-size: 5px;
}
</style>


